// Include parts of Bootstrap
@import "functions";
@import "variables";
@import "mixins";

//Base
$bs-icon-base: 1.5rem;
//lg
$bs-icon-lg: 1.7rem;
//sm
$bs-icon-sm:   1rem;
//xs
$bs-icon-xs: .6rem;
$btn-padding-y-xs: .125rem;
$btn-padding-x-xs: .25rem;
$btn-line-height-xs: 1.2;

// Button sizes
@mixin button-social($padding-y, $padding-x, $icon-height, $line-height) {
    height: $icon-height+($padding-y*2);
    padding-left: ($icon-height+$padding-x);
    & > :first-child {
        position: absolute;
        left: $padding-x/2;
        top: 0;
        padding-right: $padding-x/2;
        line-height: $line-height;
        height: $icon-height;
        font-size: $icon-height;
    }
}
@mixin btn-social-color($color-bg, $color: #fff, $opacity: 1) {
    /*color*/
    color: rgba(red($color), green($color), blue($color), $opacity) !important;
    /*mixin*/
    @include button-variant($color-bg, rgba(0,0,0, 0.2));
}
@mixin icon-size($icon-height, $padding){
    width: $icon-height+$padding;
    height: $icon-height+$padding;
    & > :first-child {
        height: $icon-height;
        font-size: $icon-height;
    }
}
/* Google font : Roboto (for google) */
@font-face {
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 400;
    src: local('Roboto'), local('Roboto-Regular'), url(https://fonts.gstatic.com/s/roboto/v18/KFOmCnqEu92Fr1Mu4mxK.woff2) format('woff2');
    unicode-range: U+0000-00FF, U+0131, U+0152-0153, U+02BB-02BC, U+02C6, U+02DA, U+02DC, U+2000-206F, U+2074, U+20AC, U+2122, U+2191, U+2193, U+2212, U+2215, U+FEFF, U+FFFD;
}

.btn-social {
    display: inline-flex !important;
    justify-content: center !important;
    position: relative;
    text-align: left;
    white-space: nowrap;
    overflow: hidden;
    & > :first-child {
        align-self: center !important;
        padding: 0;
        margin: 0;
        text-align: center;
        border-right: 1px solid rgba(0, 0, 0, 0.2);
    }
    @include button-social($btn-padding-y, $btn-padding-x, $bs-icon-base, $line-height-base)
    &.btn-lg {
        @include button-social($btn-padding-y-lg, $btn-padding-x-lg, $bs-icon-lg, $btn-line-height-lg)
    }
    &.btn-sm {
        @include button-social($btn-padding-y-sm, $btn-padding-x-sm, $bs-icon-sm, $btn-line-height-sm)
    }
    &.btn-xs {
        @include button-social($btn-padding-y-xs, $btn-padding-x-xs, $bs-icon-xs, $btn-line-height-xs)
    }
}

.btn-social-icon {
    display: inline-flex !important;
    justify-content: center !important;
    visibility: hidden;
    & > :first-child {
        align-self: center !important;
        border: none;
    }
    @include icon-size($bs-icon-base, $btn-padding-x)
    &.btn-lg {
        @include icon-size($bs-icon-lg, $btn-padding-x-lg)
    }
    &.btn-sm {
        @include icon-size($bs-icon-sm, $btn-padding-x-sm)
    }
    &.btn-xs {
        @include icon-size($bs-icon-xs, $btn-padding-x-xs)
    }
}

